import React from "react";
import AppHeader from './AppHeader/';
import AppFooter from './AppFooter/';
import { Route, Switch, HashRouter} from 'react-router-dom';
import "../../node_modules/react-vis/dist/style.css";
import {connect} from 'react-redux';
import MenuAction from '../actions/MenuAction/'
import Home from '../components/NavList/Home/';


class App extends React.Component {
  handleNavOpen = () =>{
    this.props.leftMenuOpen();
  };

  render()
  {
    return(
       <HashRouter >
      <div className= 'App' >
         <div>
         <AppHeader SideBarOpen={this.handleNavOpen} listOpenState={this.props.leftOpen}/>
         </div>

         <div className = {this.props.leftOpen?'move-right':'default'}>
             <div className = 'content'>
             <Switch>
             <Route path='/' component={Home} key='/home' exact/>

             </Switch>
             </div>
         </div>
         <div className='footer-position'>
           <AppFooter />
         </div>
      </div>
      </HashRouter>
    );
  }
}
const mapStateToProps = (state) => {
console.log(state)
  return{
    leftOpen: state.menuReducer.leftOpen,
    data: state.menuReducer.data
  }
}

const mapDispatchtoProps = (dispatch) =>{
  return{
    leftMenuOpen: () => {dispatch(MenuAction.LeftMenuOpen())},
  }
}

export default connect(mapStateToProps,mapDispatchtoProps)(App)
