import Action from '../actions/ActionID/';

const initState = {
  leftOpen : true,
}

const menuReducer = (state = initState, action) =>{
  if(action.type === Action.SHOW_LEFT_MENU)
  {
    return{
      ...state,
      leftOpen: !state.leftOpen
    }
  }
  return state;
}

export default menuReducer
